// Player specific background colors
const PLAYER_COLORS = [
    'bg-red-700/70', 'bg-blue-700/70', 'bg-green-700/70', 'bg-cyan-700/70', // Changed yellow to cyan
    'bg-purple-700/70', 'bg-pink-700/70', 'bg-teal-700/70', 'bg-orange-600/70'
];


const FiveZeroOneGame = ({ gameMode, onGameEnd, socket, gameState, displayRole }) => {
    const [playerInsults, setPlayerInsults] = React.useState({});

    // --- 1. Initial Game State Validation ---
    if (!gameState || typeof gameState !== 'object' || gameState.mode !== 'FIVE_ZERO_ONE' || !Array.isArray(gameState.participants)) {
        console.error("FiveZeroOneGame: Invalid or incomplete gameState", gameState);
        return React.createElement('div', { className: "p-8 text-center text-red-500 text-2xl sm:text-3xl" }, "Loading 501 Game State or Invalid State...");
    }

    // --- 2. Game Over Condition ---
    if (displayRole !== 'controller' && gameState.gameOver && gameState.winner) {
        if (typeof GameOverScreen !== 'undefined') {
            return React.createElement(GameOverScreen, { winner: gameState.winner, onDismiss: () => onGameEnd(false) });
        } else {
            console.warn("GameOverScreen component not available to FiveZeroOneGame for main display.");
            return React.createElement('div', {className: "p-8 text-center text-green-400 text-4xl font-sans"}, `Game Over! Winner: ${gameState.winner.name}`);
        }
    }

    // --- 3. Destructure Game State Properties ---
    const {
        participants = [], 
        currentPlayerIndex,
        currentPlayerTurnInTeam, 
        showFiveZeroOneActionPrompt,
        showKeypadForFiveZeroOne,
        bustMessage,
        history,
        doubleIn, 
        doubleOut 
    } = gameState;

    // --- 4. Current Player and Undo Logic ---
    const currentParticipant = participants[currentPlayerIndex];
    const canUndo = history && Array.isArray(history) && history.length > 1 && !showKeypadForFiveZeroOne && !bustMessage;

    // --- 5. Helper Functions ---
    const getPlayerDisplayName = React.useCallback((participant, teamMemberIndex) => {
        if (!participant) return "N/A";
        let name = participant.name || `Player ${participants.findIndex(p => p.id === participant.id) + 1}`;
        if (participant.type === 'team' && participant.players && typeof teamMemberIndex === 'number' && participant.players[teamMemberIndex]) {
            name += ` (${participant.players[teamMemberIndex]})`;
        }
        return name;
    }, [participants]);

    const getCheckoutSuggestion = React.useCallback((score) => { 
        if (score <= 1 || score > 170 || BOGEY_NUMBERS_501.includes(score)) {
            if (score > 1 && score <= 40 && score % 2 === 0) return `D${score / 2}`;
            if (score === 50) return "Bull";
            return "NO_STANDARD_OUT";
        }
        return FIVE_ZERO_ONE_CHECKOUT_CHART[score] || "CONSIDER_SETUP";
    }, []);

    // Insult Logic
    React.useEffect(() => {
        const activePlayer = participants[currentPlayerIndex]; 
        if (showKeypadForFiveZeroOne) {
             setPlayerInsults(prev => { 
                if (activePlayer && prev[activePlayer.id]) { 
                    return {
                        ...prev,
                        [activePlayer.id]: { ...prev[activePlayer.id] }
                    };
                }
                return prev;
            });
            return;
        }

        if (!participants || participants.length === 0 || currentPlayerIndex < 0 || currentPlayerIndex >= participants.length) return;
        if (!activePlayer || typeof activePlayer.score === 'undefined' || !activePlayer.id) return;
        
        const playerDisplayName = getPlayerDisplayName(activePlayer, activePlayer.type === 'team' ? currentPlayerTurnInTeam : undefined);
        let determinedInsultCategory = null; 

        if (doubleIn && activePlayer.score === FIVE_ZERO_ONE_START_SCORE_CONST) {
            determinedInsultCategory = 'doubleIn';
        } else {
            const checkoutText = getCheckoutSuggestion(activePlayer.score);
            const hasStandardCheckout = checkoutText && checkoutText !== "NO_STANDARD_OUT" && checkoutText !== "CONSIDER_SETUP";
            if (!hasStandardCheckout && activePlayer.score > 1 && activePlayer.score !== FIVE_ZERO_ONE_START_SCORE_CONST) { 
                determinedInsultCategory = 'witty';
            }
        }

        setPlayerInsults(prevPlayerInsults => {
            const currentPlayerInsultData = prevPlayerInsults[activePlayer.id] || { currentInsult: null, currentInsultCategory: null, wittyHistory: [], doubleInHistory: [] };
            let newInsultForCurrentTurn = null;
            let newInsultCategoryForState = determinedInsultCategory; 

            if (determinedInsultCategory) {
                const sourceList = determinedInsultCategory === 'witty' ? WITTY_INSULTS : DOUBLE_IN_INSULTS;
                const historyKey = determinedInsultCategory === 'witty' ? 'wittyHistory' : 'doubleInHistory';
                let playerHistory = currentPlayerInsultData[historyKey] || [];
                
                let availableInsults = sourceList.filter(template => !playerHistory.includes(template.replace('{playerName}', playerDisplayName)));

                if (availableInsults.length === 0 && sourceList.length > 0) { 
                    playerHistory = []; 
                    availableInsults = sourceList;
                }
                
                if (availableInsults.length > 0) {
                    const randomIndex = Math.floor(Math.random() * availableInsults.length);
                    const selectedInsultTemplate = availableInsults[randomIndex];
                    newInsultForCurrentTurn = selectedInsultTemplate.replace('{playerName}', playerDisplayName);
                    
                    const updatedHistory = [...playerHistory, newInsultForCurrentTurn]; 
                    if (updatedHistory.length > MAX_INSULT_HISTORY_PER_PLAYER) {
                        updatedHistory.shift(); 
                    }
                     return {
                        ...prevPlayerInsults,
                        [activePlayer.id]: {
                            ...currentPlayerInsultData,
                            currentInsult: newInsultForCurrentTurn,
                            currentInsultCategory: newInsultCategoryForState, 
                            [historyKey]: updatedHistory 
                        }
                    };
                } else { 
                    newInsultCategoryForState = null; 
                }
            }
            return {
                ...prevPlayerInsults,
                [activePlayer.id]: {
                    ...currentPlayerInsultData,
                    currentInsult: newInsultForCurrentTurn, 
                    currentInsultCategory: newInsultCategoryForState 
                }
            };
        });

    }, [ currentPlayerIndex, participants, getPlayerDisplayName, getCheckoutSuggestion, currentPlayerTurnInTeam, showKeypadForFiveZeroOne, doubleIn, gameState.bustMessage ]);


    // --- 6. Event Handlers ---
    const handleKeypadSubmit = (score) => {
        if (socket && currentParticipant) {
             socket.emit('submitFiveZeroOneScore', { score });
        }
    };
    const handleTurnAction = (action) => {
        if (action === 'no_score') handleKeypadSubmit(0);
        else if (socket) socket.emit('fiveZeroOneTurnAction', { action });
    };
    const handleKeypadCancel = () => { if (socket) socket.emit('cancelFiveZeroOneKeypad'); };
    const handleUndo = () => { if (socket && canUndo) socket.emit('undoLastAction'); };

    // --- 7. UI Configuration & Dynamic Sizing ---
    const numPlayers = participants.length;
    let cardPaddingRem = 0.5; 
    let titleFontSizeRem = 2.5; 
    let titleMarginBottomRem = 0.25;
    let mainGameAreaPaddingRem = 0.25; 
    let playerCardGapRem = 0.75;
    let teamMemberFontSizeRem = 0.875; // text-sm, for under player name
    let cardMinHeight = '12rem'; // Default for many players

    if (numPlayers <= 3) {
        playerCardGapRem = 1;
        titleFontSizeRem = 2.8;
        cardMinHeight = '18rem';
    } else if (numPlayers <= 6) {
        playerCardGapRem = 0.75;
        titleFontSizeRem = 2.5;
        cardMinHeight = '15rem';
    } else { 
        playerCardGapRem = 0.5;
        titleFontSizeRem = 2.2;
        mainGameAreaPaddingRem = 0.125;
        // cardMinHeight remains 12rem for 7+ players
    }
    
    const gameTitleToDisplay = gameMode.name || "501"; 

    // --- 8. Main Display View Rendering Function (3-Column BEERS Style) ---
    const renderMainDisplayViewFiveZeroOne = () => {
        let leadingPlayerId = null;
        if (!gameState.gameOver && participants.length > 0) {
            let minScore = FIVE_ZERO_ONE_START_SCORE_CONST + 1; 
            participants.forEach(p => {
                if (p.score < minScore) {
                    minScore = p.score;
                    leadingPlayerId = p.id;
                } else if (p.score === minScore && p.score === 0) { 
                    if (!leadingPlayerId && minScore === 0) leadingPlayerId = p.id;
                }
            });
            if (minScore > FIVE_ZERO_ONE_START_SCORE_CONST && participants.length > 0) {
                 leadingPlayerId = participants[0].id; 
            } else if (minScore === FIVE_ZERO_ONE_START_SCORE_CONST && participants.length > 0 && !participants.some(p => p.score < FIVE_ZERO_ONE_START_SCORE_CONST)){
                leadingPlayerId = participants[0].id; 
            }
        }

        return (
            React.createElement(React.Fragment, null,
                React.createElement('div', {
                    className: `game-area w-full flex flex-col h-full bg-slate-900 text-slate-100`, 
                    style: {
                        paddingTop: `${mainGameAreaPaddingRem}rem`,
                        paddingLeft: `${mainGameAreaPaddingRem}rem`,
                        paddingRight: `${mainGameAreaPaddingRem}rem`,
                        paddingBottom: '0.1rem' 
                    }
                },
                    React.createElement('h2', {
                        className: `font-black game-title-font text-yellow-300 text-center shrink-0`,
                        style: { fontSize: `${titleFontSizeRem}rem`, marginBottom: `${titleMarginBottomRem}rem` }
                    }, gameTitleToDisplay),

                    React.createElement('div', {
                        className: `grid grid-cols-1 flex-grow min-h-0 overflow-y-auto custom-scrollbar pr-0.5 sm:pr-1 pb-1`, 
                        style: { gap: `${playerCardGapRem}rem` }
                    },
                        participants.map((p, index) => {
                            if (!p || typeof p !== 'object') return null;
                            const isCurrent = index === currentPlayerIndex;
                            const isLeading = p.id === leadingPlayerId && p.score < FIVE_ZERO_ONE_START_SCORE_CONST && p.score > 0; 
                            const hasPossibleCheckout = p.score > 1 && p.score <= 170 && !BOGEY_NUMBERS_501.includes(p.score);
                            const needsDoubleInUI = doubleIn && p.score === FIVE_ZERO_ONE_START_SCORE_CONST;

                            // Assign base player color
                            let baseBgColor = PLAYER_COLORS[index % PLAYER_COLORS.length];
                            let ringStyle = 'border-2 border-slate-600'; 
                            let shadowStyle = 'shadow-xl'; 
                            let nameColor = 'text-slate-100'; 
                            let scoreColor = 'text-sky-200'; 
                            let pulseAnimation = '';
                            let currentTurnTextColor = 'text-white'; 

                            if (isCurrent) {
                                baseBgColor = 'bg-indigo-700'; 
                                ringStyle = 'ring-2 sm:ring-4 ring-indigo-400'; 
                                shadowStyle = 'shadow-2xl shadow-indigo-500/50'; 
                                nameColor = currentTurnTextColor; 
                                scoreColor = currentTurnTextColor; 
                                if (bustMessage && showFiveZeroOneActionPrompt && displayRole !== 'controller' && gameState.participants[gameState.currentPlayerIndex]?.id === p.id) { 
                                    baseBgColor = 'bg-red-600'; 
                                    ringStyle = 'ring-2 sm:ring-4 ring-red-400';
                                    pulseAnimation = 'animate-pulse';
                                } else if (needsDoubleInUI) {
                                     baseBgColor = 'bg-orange-600'; 
                                     ringStyle = 'ring-2 sm:ring-4 ring-orange-400';
                                     pulseAnimation = 'animate-pulse';
                                }
                            } else if (p.score === 0) { 
                                baseBgColor = 'bg-green-500'; 
                                nameColor = 'text-white';
                                scoreColor = 'text-yellow-300'; 
                                ringStyle = 'ring-2 sm:ring-4 ring-yellow-400'; 
                                pulseAnimation = 'animate-pulse';
                            } else if (isLeading) {
                                ringStyle = 'ring-2 ring-emerald-400'; 
                            }
                            
                            if (!isCurrent && p.score !== 0) {
                                const playerBg = PLAYER_COLORS[index % PLAYER_COLORS.length];
                                const isLightBg = playerBg.includes('yellow') || playerBg.includes('pink') || playerBg.includes('orange-600') || playerBg.includes('cyan-600');
                                if (isLightBg) { 
                                    nameColor = 'text-black'; 
                                    scoreColor = 'text-black';
                                } else { 
                                    nameColor = 'text-slate-100'; 
                                    scoreColor = 'text-sky-200';
                                }
                            }

                            let cardClasses = `player-card ${baseBgColor} ${ringStyle} ${shadowStyle} ${pulseAnimation} rounded-lg sm:rounded-xl flex flex-col items-center justify-start transition-all duration-300`; 

                            const currentInsultData = playerInsults[p.id];
                            const messageToDisplay = isCurrent && !showKeypadForFiveZeroOne && currentInsultData ? currentInsultData.currentInsult : null;
                            const currentCategoryForDisplay = isCurrent && !showKeypadForFiveZeroOne && currentInsultData ? currentInsultData.currentInsultCategory : null;
                            const checkoutText = getCheckoutSuggestion(p.score);
                            
                            let column3Content = null;
                            const insultBaseClasses = "px-1 text-3xl md:text-4xl leading-tight font-bold text-black"; 

                            if (p.score === 0) {
                                column3Content = React.createElement('p', { className: `font-bold text-yellow-300 animate-bounce text-2xl md:text-3xl leading-snug` }, `${ICON_GAME_SHOT} GAME SHOT!`);
                            } else if (messageToDisplay && currentCategoryForDisplay === 'doubleIn' && needsDoubleInUI) {
                                column3Content = React.createElement('p', { className: `${insultBaseClasses}` }, messageToDisplay);
                            } else if (messageToDisplay && currentCategoryForDisplay === 'witty' && !needsDoubleInUI && p.score > 1) {
                                column3Content = React.createElement('p', { className: `${insultBaseClasses}` }, messageToDisplay);
                            } else if (checkoutText && checkoutText !== "NO_STANDARD_OUT" && checkoutText !== "CONSIDER_SETUP") {
                                column3Content = React.createElement('p', { className: `font-semibold text-emerald-200 text-5xl md:text-5xl leading-snug` }, `Out: ${checkoutText.replace(/T/g, 'T').replace(/D/g, 'D')}`);
                            } else if (p.score === 1) {
                                column3Content = React.createElement('p', { className: `font-bold text-red-300 text-xl md:text-2xl leading-snug` }, `${ICON_BUST} BUSTED! (No Out)`);
                            }

                            // MODIFIED: Score styling
                            let scoreShadowColorValue = 'rgba(0,0,0,0.35)'; // Default dark shadow for light text
                            if (scoreColor.includes('text-black')) {
                                scoreShadowColorValue = 'rgba(255,255,255,0.35)'; // Light shadow for dark text
                            }
                            const scoreBaseClassName = `font-mono font-black ${scoreColor} tracking-tighter leading-none`;
                            const scoreStyle = { 
                                fontSize: 'clamp(3rem, 11vw, 6.5rem)', 
                                textShadow: `3px 3px 0px ${scoreShadowColorValue}` 
                            };

                            return (
                                React.createElement('div', { 
                                    key: p.id || index, 
                                    className: cardClasses, 
                                    style: { padding: `${cardPaddingRem}rem`, minHeight: cardMinHeight } 
                                },
                                    React.createElement('div', { className: "flex flex-row items-stretch justify-between w-full h-full" },
                                        // Column 1: Player Name & Status (Double In)
                                        React.createElement('div', { className: "flex flex-col justify-center items-center text-center p-1 md:p-2 w-[30%]" }, 
                                            React.createElement('div', {
                                                className: `font-bold ${nameColor} break-words leading-none flex items-center justify-center text-center`, 
                                                style: { fontSize: 'clamp(2rem, 7vw, 4.5rem)' } 
                                            },
                                                isLeading && !isCurrent && p.score > 0 && React.createElement('span', { className: "text-yellow-300", style: { fontSize: 'clamp(1.5rem, 6vw, 3.5rem)', marginRight: '0.25rem'} }, ICON_LEADER),
                                                getPlayerDisplayName(p, p.type === 'team' && isCurrent ? currentPlayerTurnInTeam : (p.type === 'team' ? 0 : undefined))
                                            ),
                                            p.type === 'team' && isCurrent && p.players && p.players[currentPlayerTurnInTeam] &&
                                                React.createElement('div', { className: `${nameColor} opacity-80 mt-1`, style: {fontSize: `${teamMemberFontSizeRem}rem`} }, `(Thrower: ${p.players[currentPlayerTurnInTeam]})`),
                                            needsDoubleInUI && (!column3Content || currentCategoryForDisplay !== 'doubleIn') && 
                                                React.createElement('p', { className: `font-semibold ${isCurrent ? 'text-orange-200' : 'text-orange-400'} ${isCurrent ? 'animate-pulse' : ''} mt-1 text-3xl md:text-4xl leading-tight` }, `${ICON_DOUBLE_IN_WARN} Needs Double-In!`)
                                        ),

                                        // Column 2: Current Score
                                        React.createElement('div', { className: "flex flex-col justify-center items-center text-center p-1 md:p-2 w-[20%] border-l border-r border-slate-500/50"}, 
                                            React.createElement('div', {
                                                className: scoreBaseClassName, // Use new base class
                                                style: scoreStyle // Use new style object
                                            }, p.score)
                                        ),

                                        // Column 3: Dynamic Content (Insults / Checkout / Status)
                                        React.createElement('div', { className: "flex flex-col justify-center items-center text-center p-1 md:p-2 w-[50%]"}, 
                                            column3Content 
                                        )
                                    )
                                )
                            );
                        })
                    ),
                    currentParticipant && !gameState.gameOver && bustMessage && showFiveZeroOneActionPrompt && displayRole !== 'controller' && (
                        React.createElement('div', { className: "fixed inset-x-0 bottom-10 flex justify-center items-center z-50" }, 
                            React.createElement('div', { className: "text-center p-3 sm:p-4 bg-red-700 text-white rounded-lg shadow-xl max-w-md animate-bounce" },
                                React.createElement('h3', { className: "text-2xl sm:text-3xl font-bold mb-1" }, `${ICON_BUST} ${bustMessage.toUpperCase()}!!!`),
                                React.createElement('p', { className: "text-sm sm:text-base" }, `${getPlayerDisplayName(currentParticipant, currentPlayerTurnInTeam)} to acknowledge on controller.`)
                            )
                        )
                    )
                )
            )
        );
    };

    // --- 9. Controller View Rendering Function (BEERS Style Buttons) ---
    const renderControllerViewFiveZeroOne = () => {
        const controllerPrimaryButtonClass = "w-36 h-36 text-xl sm:text-2xl font-bold rounded-lg shadow-md my-2 transition-transform hover:scale-105 active:scale-95 flex items-center justify-center text-center leading-tight";
        const controllerSecondaryButtonClass = "w-32 h-32 text-lg font-bold rounded-lg shadow-md my-2 transition-transform hover:scale-105 active:scale-95 flex items-center justify-center text-center leading-tight";
        
        const controllerInfoTextClass = "text-lg sm:text-xl text-center text-slate-300 mb-1";
        const controllerPlayerNameClass = "text-2xl sm:text-3xl text-yellow-300 font-semibold text-center mb-2 game-title-font leading-tight";
        const controllerScoreClass = "text-6xl sm:text-7xl font-black text-sky-300 my-1 text-center [text-shadow:0_0_8px_rgba(56,189,248,0.5)]";
        const ActualKeypadComponent = window.Keypad;

        if (!currentParticipant) {
            return React.createElement('div', { className: "flex flex-col items-center justify-center h-full p-4 bg-slate-800 rounded-lg" },
                React.createElement('p', { className: controllerInfoTextClass }, "Waiting for current player data...")
            );
        }
        const displayName = getPlayerDisplayName(currentParticipant, currentPlayerTurnInTeam);
        const isBusted = showFiveZeroOneActionPrompt && bustMessage && !showKeypadForFiveZeroOne;
        const needsToDoubleInMessageController = doubleIn && currentParticipant.score === FIVE_ZERO_ONE_START_SCORE_CONST;
        const canEnterScore = showFiveZeroOneActionPrompt && !isBusted && !showKeypadForFiveZeroOne;

        return React.createElement('div', { className: "flex flex-col items-center justify-around h-full p-3 space-y-2 bg-slate-800 rounded-lg shadow-2xl" }, 
            React.createElement('div', { className: "w-full text-center" },
                React.createElement('h3', { className: controllerPlayerNameClass }, `Turn: ${displayName}`),
                React.createElement('p', { className: controllerScoreClass }, currentParticipant.score),
                needsToDoubleInMessageController && 
                    React.createElement('p', {className: `${controllerInfoTextClass} text-orange-400 font-semibold animate-pulse`}, `${ICON_DOUBLE_IN_WARN} Needs Double-In!`),
                currentParticipant.score > 0 && currentParticipant.score <= 170 && (!doubleOut || (doubleIn && currentParticipant.score < FIVE_ZERO_ONE_START_SCORE_CONST) || !doubleIn ) && 
                    React.createElement('p', {className: `${controllerInfoTextClass} text-emerald-300`},
                        "Out: ", React.createElement('strong', { className: "text-lg" }, getCheckoutSuggestion(currentParticipant.score).replace(/T/g, 'T').replace(/D/g, 'D'))
                    )
            ),
            React.createElement('div', { className: "w-full max-w-xs space-y-3 flex flex-col items-center" },
                canEnterScore && React.createElement(React.Fragment, null, 
                    React.createElement('button', { 
                        onClick: () => handleTurnAction('score_counts'), 
                        className: `${controllerPrimaryButtonClass} bg-sky-500 hover:bg-sky-600 text-white` 
                    }, `${ICON_DART_TARGET} Enter Score`),
                    React.createElement('button', { 
                        onClick: () => handleTurnAction('no_score'), 
                        className: `${controllerSecondaryButtonClass} bg-slate-600 hover:bg-slate-700 text-white` 
                    }, `${ICON_MISS_SHRUG} No Score`)
                ),
                isBusted && React.createElement('div', { className: "text-center w-full p-3 bg-red-800/60 rounded-lg flex flex-col items-center" },
                    React.createElement('p', { className: "text-2xl sm:text-3xl text-red-200 font-bold animate-bounce" }, `${ICON_BUST} ${bustMessage.toUpperCase()}!!!`),
                    React.createElement('button', { 
                        onClick: () => handleTurnAction('bust_acknowledged'), 
                        className: `${controllerSecondaryButtonClass} bg-orange-500 hover:bg-orange-600 text-white mt-2`
                    }, `${ICON_NEXT_PLAYER_OK} OK`)
                ),
                showKeypadForFiveZeroOne && showKeypadForFiveZeroOne.participantIndex === currentPlayerIndex && ActualKeypadComponent &&
                    React.createElement(ActualKeypadComponent, { 
                        onSubmit: handleKeypadSubmit, 
                        onCancel: handleKeypadCancel, 
                        title: `Score for ${displayName}`
                    },
                     React.createElement('div', { className: "text-center text-sm text-gray-300 mb-2" }, 
                        "Enter total score for 3 darts.",
                         needsToDoubleInMessageController && React.createElement('p', {className: "text-orange-300 font-semibold mt-0.5"}, `${ICON_DOUBLE_IN_WARN} Double-In required!`),
                        doubleOut && currentParticipant.score <= 170 && currentParticipant.score > 1 && !BOGEY_NUMBERS_501.includes(currentParticipant.score) && (currentParticipant.score % 2 === 0 || currentParticipant.score === 50) &&
                            React.createElement('p', {className: "text-green-300 font-semibold mt-0.5"}, `${ICON_DART_TARGET} Double-Out required!`)
                     )
                ),
                canUndo && React.createElement('button', { 
                    onClick: handleUndo, 
                    className: `${controllerSecondaryButtonClass} bg-yellow-500 hover:bg-yellow-600 text-black`
                }, `${ICON_UNDO} Undo`)
            )
        );
    };

    // --- 10. Conditional Rendering Based on displayRole ---
    if (displayRole === 'controller') {
        return renderControllerViewFiveZeroOne();
    } else {
        return renderMainDisplayViewFiveZeroOne();
    }
};
